.sidebar {
    display: block;
    max-width: 340px;
    min-width: 250px;
    padding-left: 15px;
    padding-right: 45px;
    font-family: 'Roboto';
    line-height: 1.4;
    color:#2c2f34;   
    z-index: 1;
    .container-wrapper {
        margin-bottom: 30px; 
        padding: 30px;
        box-sizing: border-box;
        border: 1px solid rgba(0,0,0,0.1);
        .the-subtitle {
            font-family: "Montserrat";
            position: relative;
            font-size: 15px;
            min-height: 15px;
            font-weight: 600;
            padding-bottom: 15px;
            color: #2c2f34;
            border-bottom: 2px solid rgba(0,0,0,0.1);
            &::before {
                content: "";
                width: 0;
                height: 0;
                position: absolute;
                bottom: -5px;
                left: 0;
                border-left: 0;
                border-right: 5px solid transparent;
                border-top: 5px solid #2c2f34;
            }
            &::after {
                content: "";
                background: #2c2f34;
                width: 40px;
                height: 2px;
                position: absolute;
                bottom: -2px;
                left: 0;
            }
        }
        h4 {
            font-family: "Montserrat", sans-serif;
            font-size: 17px !important;
            font-weight: 600;
            line-height: 24px;
            margin-bottom: 10px;
            margin-top: 20px;
            color: #2c2f34;
        }
        ul {
            padding: 0;
            margin-top: 0;
            list-style-type: none;
            margin-bottom: 30px;
            li {
                font-size: 13.5px;
                line-height: 21px;
                padding: 5px 0 5px 12px;
                position: relative;
                &:first-child {
                    padding: 0 0 5px 12px;
                    &::before {
                        content: "";
                        width: 5px;
                        border-top: 1px solid rgba(0,0,0,0.8);
                        position: absolute;
                        left: 0;
                        top: 8px;
                        transform: rotate(45deg);
                    }
                    &::after {
                        content: "";
                        width: 5px;
                        border-top: 1px solid rgba(0,0,0,0.8);
                        position: absolute;
                        left: 0;
                        top: 11px;
                        transform: rotate(-45deg);
                    }
                }
                &:last-child {
                    padding: 5px 0 0 12px;
                   
                }
                a {
                    text-decoration: none;
                    color: #333;
                }
                &:hover {
                    a {
                        color: #591F3D;
                    }
                }
                &::before {
                    content: "";
                    width: 5px;
                    border-top: 1px solid rgba(0,0,0,0.8);
                    position: absolute;
                    left: 0;
                    top: 13px;
                    transform: rotate(45deg);
                }
                &::after {
                    content: "";
                    width: 5px;
                    border-top: 1px solid rgba(0,0,0,0.8);
                    position: absolute;
                    left: 0;
                    top: 16px;
                    transform: rotate(-45deg);
                }
            }
        }
    }
    .recente {
        margin-bottom: 0;
        position: sticky;
        top: 20px;
        .the-subtitle {
            color: #2c2f34;
            line-height: 24px;
        }
        .recente-main {
            a {
                position: relative;
                bottom: 3px;
                img {
                    max-width: 275px;
                    width: 100%;
                    height: auto;
                }
                span {
                    position: absolute;
                    background-color: #7c284e;
                    color: #FFFFFF;
                    padding: 0 6px;
                    font-size: 12px;
                    line-height: 1.85em;
                    bottom: 10px;
                    right: 10px;
                    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", "Open Sans", Arial, sans-serif;
                }
            }
            &:hover {
                a {
                    img {
                        transition: all 0.3s;
                        opacity: 0.85;
                    }  
                }
            }
        }
        .title {
            color: #333;
            text-decoration: none;
            transition: 0.15s;
            font-size: 17px;
            line-height: 24px;
            font-weight: 600;
            margin-bottom: 20px;
        }
        a.title-link {
            color: #333;
            text-decoration: none;
            transition: 0.15s;
            font-family: "Montserrat";
            font-size: 18px;
            line-height: 1.4;
            font-weight: 600;
            display: block;
            margin-bottom: 27px;
            &:hover {
                color: #7c284e;
            }
        }
        .post {
            padding: 7px 0;
            a {
                display: flex;
                justify-content: center;
                text-decoration: none;
                color: #333;
                img {
                    width: 110px;
                    height: 75px;
                    margin-right: 15px;
                }
                span {
                    font-family: "Montserrat";
                    display: block;
                    font-size: 14px;
                    line-height: 1.4;
                    font-weight: 600;
                    overflow-wrap: break-word;
                    margin-left: auto;
                }
            }
            &:hover {
                a {
                    color: #7c284e; 
                    img {
                        transition: all 0.3s;
                        opacity: 0.85;
                    }  
                }
            }
            &:last-child {
                padding-bottom: 0;
            }
        }
        @media (max-width: 1110px) {
            .post:nth-child(3),
            .post:nth-child(4) {
                margin-left: -12px;
            }
        }
    }
}
@media (max-width:991px) {
    .sidebar {
        max-width: 991px;
        margin-top: 0;
        width: 100%;
        padding: 20px;
        padding-bottom: 0;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        .recente {
            .recente-content {
                display: flex;
                .main-container {
                    width: 50%;
                    margin-right: 30px;
                    img {
                        max-width: 350px;
                    }
                }
                .posts {
                    width: 50%;
                    .post {
                        padding: 7px 0;
                        min-height: auto;
                        a {
                            justify-content: flex-start;
                            img {
                                max-width: 110px;
                                width: 100%;
                                height: auto;
                            }
                            span {
                                margin-left: 0;
                                height: auto;
                                font-weight: 600;
                            }
                        }
                        &:last-child{
                            display: none;
                        }
                    }
                }
            }
        }
    }
}
@media (max-width:767px) {
    .sidebar {
        .container-wrapper {
            padding: 20px 15px;;
        }
    }
}
@media (max-width:670px) {
    .sidebar {
        .recente {
            .recente-content {
                display: flex;
                flex-direction: column;
                .main-container {
                    width: 100%;
                    margin-right: 0;
                    img {
                        max-width: 560px;
                    }
                }
                .posts {
                    width: 100%;
                    .post {
                        &:last-child{
                            display: block;
                        }
                    }
                }
            }
        }
    }
}